J"},[_vm._v(" KYC verification is when a user's information is verified against documents issued by authorities or against information obtained from independent and reliable sources to ensure the user has not provided false documents or incorrect information. It is mainly a regulatory process used to verify the identity and other credentials of a user to prevent fraud and to comply with KYC and AML regulations now mandatory in many countries. ")]),_c('p',{staticClass:"sc-1578630c-0 sc-c32b30f7-5 jloYPS binOuJ"},[_c('span',{staticClass:"sc-c32b30f7-0 fwGerd"},[_c('strong',[_vm._v("How is the KYC verification process conducted?")])])]),_c('p',{staticClass:"sc-1578630c-0 sc-c32b30f7-5 jloYPS binOuJ"},[_vm._v(" The user's information is verified against documents issued by authorities or against information obtained from independent and reliable sources to ensure the user has not provided false documents or incorrect information. ")]),_c('p',{staticClass:"sc-1578630c-0 sc-c32b30f7-5 jloYPS binOuJ"},[_c('span',{staticClass:"sc-c32b30f7-0 fwGerd"},[_c('strong',[_vm._v("Types of KYC Verification?")])])]),_c('p',{staticClass:"sc-1578630c-0 sc-c32b30f7-5 jloYPS binOuJ"},[_vm._v(" Verification of documents can be done manually or automatically through a recognised third party verification platform. Manual verification takes time, is prone to errors and might not spot increasingly advanced forged documents. Therefore, an automatic approach that compares documents against various open data sources and performs checks for optimal security features is used to speed up the process and minimise the risk of any fraudulent activities. ")]),_c('p',{staticClass:"sc-1578630c-0 sc-c32b30f7-5 jloYPS binOuJ"},[_vm._v(" Sumsub, an all-in-one verification platform (https://sumsub.com), performs the verification of the user’s documents. The platform stores the copies of the documents presented by the user in an encrypted format on secure servers that comply with GDPR requirements. Sumsub plat